import React from 'react'
import { workExperience } from '../json/dataSource.json'

export default function WorkExp({ id }) {
  return (
    <div id={id} className="working-exp">
      <h3>Work Experience</h3>
      {workExperience.length > 0 ? (
        <ul>
          {workExperience.map((exp, key) => (
            <li key={key}>
              <div className="year">{`${exp.dateIn} - ${exp.dateOut}`}</div>
              <div className="list">{exp.title}</div>
              <div className="company-name">{exp.companyName}</div>
            </li>
          ))}
        </ul>
      ) : (
        <span>No Experience to show</span>
      )}
    </div>
  )
}
