import React from 'react'
import { educations } from '../json/dataSource.json'

export default function Educations({ id }) {
  console.log(educations)
  return (
    <div id={id} className="educations">
      <h3>Education</h3>
      <ul>
        {educations && educations.length > 0
          ? educations.map((edu, i) => (
              <li key={i}>
                <div className="year">{`${edu.yearIn} - ${edu.yearOut}`}</div>
                <div className="list">{edu.institution}</div>
                <div className="degree">{`${edu.title} ${
                  edu.degree ? `- ${edu.degree}` : ''
                }`}</div>
              </li>
            ))
          : 'Nothing to show here'}
      </ul>
    </div>
  )
}
