import React from 'react'
import { skills } from '../json/dataSource.json'

const Skills = ({ id }) => {
  return (
    <div id={id} className="skills">
      <h3>Skills</h3>
      {skills.length > 0 ? (
        <ul>
          {skills.map((skill, key) => (
            <li key={key}>{skill}</li>
          ))}
        </ul>
      ) : (
        <span>Nothing to show</span>
      )}
    </div>
  )
}

export default Skills
