import React, { Component } from 'react'
import { Row, Col, Container, Button } from 'reactstrap'
import { Link as SLink, animateScroll as scroll } from 'react-scroll'
import WOW from 'react-wow'

import pp from '../assets/images/profile.jpg'
import Information from './Information'
import Profile from './Profile'
import Educations from './Educations'
import Skills from './Skills'
import WorkExp from './WorkExp'
import Portfolios from './Portfolios'
import Licenses from './Licenses'

const GitCalendar = require('github-calendar')

export default class Home extends Component {
  componentDidMount() {
    GitCalendar('.calendar', 'gundwiguna', { responsive: true })
  }

  render() {
    return (
      <Container className="home">
        <Row className="header-row">
          <Col className="header-container pb-2 text-center shadow">
            <div className="text-center name-header">I Made Anggun Dwiguna</div>
          </Col>
        </Row>
        <Row className="body-column">
          <Col lg="3" className="py-2 shadow side-container">
            <div className="d-flex flex-column align-items-center">
              <WOW animation="bounceInUp">
                <div
                  className="profile-picture my-4 "
                  style={{
                    backgroundImage: `url(${pp})`,
                    backgroundPosition: 'center',
                    backgroundSize: 'contain',
                  }}
                />
              </WOW>
              <WOW animation="bounceInUp" delay="250ms">
                <div>
                  <Information />
                </div>
              </WOW>
              <WOW animation="bounceInUp" delay="500ms">
                <div className="side-menu py-3 d-flex flex-column align-items-center border-bottom">
                  <SLink smooth to="profile">
                    Profile
                  </SLink>
                  <SLink smooth to="education">
                    Education
                  </SLink>
                  <SLink smooth to="skills">
                    Skills
                  </SLink>
                  <SLink smooth to="workingexp">
                    Work Experience
                  </SLink>
                  <SLink smooth to="licenses">
                    {`License & Certification`}
                  </SLink>
                  <SLink smooth to="portfolios">
                    Portfolio
                  </SLink>
                  <SLink smooth to="git">
                    GitHub Summary
                  </SLink>
                </div>
              </WOW>
            </div>
          </Col>
          <Col lg="9" className="py-4 content-container">
            <Container>
              <WOW animation="bounceInUp">
                <div>
                  <Profile id="profile" />
                  <hr />
                </div>
              </WOW>
              <WOW animation="bounceInUp" delay="150ms">
                <div>
                  <Educations id="education" />
                  <hr />
                </div>
              </WOW>
              <WOW animation="bounceInUp" delay="300ms">
                <div>
                  <Skills id="skills" />
                  <hr />
                </div>
              </WOW>
              <WOW animation="bounceInUp" delay="450ms">
                <div>
                  <WorkExp id="workingexp" />
                  <hr />
                </div>
              </WOW>
              <WOW animation="bounceInUp" delay="150ms">
                <div>
                  <Licenses id="licenses" />
                  <hr />
                </div>
              </WOW>
              <WOW animation="bounceInUp" delay="300ms">
                <div>
                  <Portfolios id="portfolios" />
                  <hr />
                </div>
              </WOW>
              <WOW animation="bounceInUp" delay="300ms">
                <h3 id="git" className="my-3">
                  My GitHub Summary
                </h3>
                <div className="calendar" />
                <hr />
              </WOW>
              <div className="d-flex justify-content-center">
                <Button color="primary" size="sm" onClick={() => scroll.scrollToTop()}>
                  To Top <i className="fas fa-arrow-up" />
                </Button>
              </div>
            </Container>
          </Col>
        </Row>
        <Row>
          <Col className="footer d-flex flex-column align-items-center py-4">
            <div className="py-3">
              <i
                onClick={() => window.open('https://facebook.com/gundwiguna')}
                className="fab fa-facebook mx-2"
              />
              <i
                onClick={() => window.open('https://github.com/gundwiguna')}
                className="fab fa-github mx-2"
              />
              <i
                onClick={() => window.open('https://linkedin.com/in/anggun-dwiguna-53197511a/')}
                className="fab fa-linkedin mx-2"
              />
              <i
                onClick={() => window.open('https://instagram.com/gundwiguna')}
                className="fab fa-instagram mx-2"
              />
            </div>
            <span className="footer-text">2019 - Anggun Dwiguna - Github Page</span>
          </Col>
        </Row>
      </Container>
    )
  }
}
